<style scoped>
  @import 'assets/css/common.css';
	@import 'assets/css/teacher.css';
	.teacher_body{
		width: 1200px;
		height: auto;
	}
</style>
<template>
	<div id="teacher">
		<navbar navNum="2"></navbar>
		<teacherList v-bind:teacherInfo="teacherInfo"></teacherList>
	</div>
</template>
<script>
import navbar from '@/components/header'
import teacherList from '@/components/teacher'

export default {
  name: 'app',
  data () {
    return {
      teacherInfo: [
        {id: '01', img: './static/images/teacher1.jpg', name: '', department: '', descript: 'java,'},
        {id: '02', img: './static/images/teacher2.jpg', name: '', department: '', descript: ','},
        {id: '03', img: './static/images/teacher3.jpg', name: '', department: '', descript: ','},
        {id: '04', img: './static/images/teacher4.jpg', name: '', department: '', descript: 'photoShop,'},
        {id: '05', img: './static/images/teacher5.jpg', name: '', department: '2', descript: 'java,2'},
        {id: '06', img: './static/images/teacher1.jpg', name: '', department: '', descript: ',2'},
        {id: '07', img: './static/images/teacher2.jpg', name: '', department: '', descript: ',2'},
        {id: '08', img: './static/images/teacher6.jpg', name: '', department: '2', descript: 'photoShop,2'}
      ]
    }
  },
  components: {
    navbar,
    teacherList
  }
}
</script>


