<style scoped>
	@import 'assets/css/common.css';
	@import 'assets/css/teacher_detail.css';
</style>
<template>
	<div id="teacherDetail">
		<navbar navNum="2"></navbar>
    	<div class="container">
            <div class="intro_teacher">
                <p class="title"></p>
                <div v-for="(item, index) in teacherData" v-if="item.id == teacherId">
	                <div class="intro_body mTop30 clearfix">
	                    <div class="pic pull-left">
	                        <img v-bind:src="item.img" alt="">
	                    </div>
	                    <div class="intro pull-right">
	                        <p class="name mTop10">{{item.name}}</p>
	                        <p class="job mTop20">{{item.department}}</p>
	                        <p class="con mTop20">{{item.descript}}</p>
	                    </div>
	                </div>
	                <div class="course mTop30">
	                    <p class="title"></p>
                      <courseList v-bind:courseData="item.courseData"></courseList>
	                </div>
                </div>
            </div>
    	</div>
	</div>
</template>
<script>
  import navbar from '@/components/header'
  import courseList from '@/components/courseList'
  export default({
    name: 'app',
    components: {
      navbar,
      courseList
    },
    data () {
      return {
        teacherId: this.$route.query.teacherId,
        teacherData: [
          {id: '01', img: './static/images/teacher1.jpg', name: '', department: '', descript: 'java,,', courseData: [{id: '000', imgurl: './static/images/kc0/kc1.jpg', title: '1'}, {id: '001', imgurl: './static/images/kc0/kc2.jpg', title: '2'}]},
        {id: '02', img: './static/images/teacher2.jpg', name: '', department: '', descript: ',,', courseData: [{id: '002', imgurl: './static/images/kc0/kc3.jpg', title: '3'}]},
        {id: '03', img: './static/images/teacher3.jpg', name: '', department: '', descript: ',,', courseData: [{id: '010', imgurl: './static/images/kc1/kc1.jpg', title: '1'}]},
        {id: '04', img: './static/images/teacher4.jpg', name: '', department: '', descript: 'photoShop,,', courseData: [{id: '011', imgurl: './static/images/kc1/kc2.jpg', title: '2'}]},
        {id: '05', img: './static/images/teacher5.jpg', name: '', department: '2', descript: 'java,,', courseData: [{id: '010', imgurl: './static/images/kc1/kc1.jpg', title: '1'}]},
        {id: '06', img: './static/images/teacher1.jpg', name: '', department: '', descript: 'java,,', courseData: [{id: '000', imgurl: './static/images/kc0/kc1.jpg', title: '1'}, {id: '001', imgurl: './static/images/kc0/kc2.jpg', title: '2'}]},
        {id: '07', img: './static/images/teacher2.jpg', name: '', department: '', descript: ',,', courseData: [{id: '002', imgurl: './static/images/kc0/kc3.jpg', title: '3'}]},
        {id: '08', img: './static/images/teacher6.jpg', name: '', department: '2', descript: ',,', courseData: [{id: '010', imgurl: './static/images/kc1/kc1.jpg', title: '1'}]}
        ]
      }
    }
  })
</script>


