import Vue from 'vue'
import Router from 'vue-router'
import Index from '@/index'
import CourseDetail from '@/courseDetail'
import Course from '@/course'
import Teacher from '@/teacher'
import Study from '@/study'
import Qa from '@/qa'
import Comment from '@/comment'
import TeacherDetail from '@/TeacherDetail'
import Personal from '@/personal'
Vue.use(Router)

export default new Router({
  routes: [
    { path: '/index', name: '', component: Index },
    { path: '/courseDetail', name: '', component: CourseDetail },
    { path: '/course', name: '', component: Course },
    { path: '/teacher', name: '', component: Teacher },
    { path: '/study', name: '', component: Study },
    { path: '/qa', name: '', component: Qa },
    { path: '/comment', name: '', component: Comment },
    { path: '/teacherDetail', name: '', component: TeacherDetail },
    { path: '/personal', name: '', component: Personal },
    { path: '/', redirect: '/index' }
  ]
})



