<style scoped>
	@import 'assets/css/common.css';
	@import 'assets/css/course_detail.css';
</style>
<template>
	<div id="courseDetail">
		<navbar v-bind:navNum="1"></navbar>
		<courseHead v-bind:courseId="courseId"></courseHead>
		<div v-for="(item, index) in detailInfo" v-if="item.id == courseId">
		    <!--  -->
		    <div class="content">
		        <div class="content_left">
		            <div class="kecheng_jieshao">
		                <h3></h3>
		                <p class="jieshao">{{item.intro}}</p>
		            </div>
		            <div class="kecheng_line">
		                <h3></h3>
		                <ul class="article">
		                    <li v-for="(itemCon, index) in item.courseList">
		                        <div>
		                            <h3 class="title">{{itemCon.subject}}</h3>
		                            <p>{{itemCon.con}}</p>
		                        </div>
		                    </li>
		                </ul>
		            </div>
		        </div>
		        <div class="content_right">
		        	<courseRight></courseRight>
		        	<button class="studay_btn" v-on:click="$router.push({path: './study?courseId', query: {courseId: courseId}})"></button>
		        </div>
		        <div class="clearfix"></div>
		        <div class="student_comment">
		            <h3></h3>
		            <hudong v-bind:hdData="hdData"></hudong>
		        </div>
		    </div>
		</div>
	</div>
</template>
<script>
import navbar from '@/components/header'
import hudong from '@/components/hudong'
import courseHead from '@/components/courseHead'
import courseRight from '@/components/courseRight'

export default {
  name: 'app',
  components: {
    navbar,
    hudong,
    courseHead,
    courseRight
  },
  data () {
    return {
      courseId: '',
      hdData: [
        {pic: './static/images/tx-3.png', questioner: '2', state: '', teacher: '', link: '', course: 'java2', con: ' (Overriding)Java'},
        {pic: './static/images/tx-1.png', questioner: '2', state: '', teacher: '', link: '', course: '2', con: ''},
        {pic: './static/images/tx-2.png', questioner: '2', state: '', teacher: '', link: '', course: 'C2', con: ''},
        {pic: './static/images/tx-5.png', questioner: '2', state: '', teacher: '', link: '', course: '2', con: ''},
        {pic: './static/images/tx-4.png', questioner: '2', state: '', teacher: '', link: '', course: '2', con: ''}
      ],
      detailInfo: [
        {
          id: '000',
          intro: '111111',
          courseList: [
            { subject: '11', con: '11' },
            { subject: '12', con: '12' },
            { subject: '13', con: '13' },
            { subject: '14', con: '14' },
            { subject: '15', con: '15' }
          ]
        },
        {
          id: '001',
          intro: '222222',
          courseList: [
            { subject: '21', con: '21' },
            { subject: '22', con: '22' },
            { subject: '23', con: '23' }
          ]
        },
        {
          id: '002',
          intro: '333333',
          courseList: [
            { subject: '31', con: '31' },
            { subject: '32', con: '32' },
            { subject: '33', con: '33' },
            { subject: '34', con: '34' },
            { subject: '35', con: '35' },
            { subject: '36', con: '36' }
          ]
        },
        {
          id: '010',
          intro: '111111',
          courseList: [
            { subject: '11', con: '11' },
            { subject: '12', con: '12' },
            { subject: '13', con: '13' },
            { subject: '14', con: '14' }
          ]
        },
        {
          id: '011',
          intro: '222222',
          courseList: [
            { subject: '21', con: '21' },
            { subject: '22', con: '22' },
            { subject: '23', con: '23' },
            { subject: '24', con: '24' }
          ]
        }
      ]
    }
  },
  mounted () {
    this.courseId = this.$route.query.courseId
  }
}
</script>


