<style scoped>
  @import 'assets/css/index.css';
</style>
<template>
  <div>
    <navbar navNum="-1"></navbar>
    <!--  -->
    <div class="lunbo">
        <div id="boxgallery" class="boxgallery" data-effect="effect-2">
            <div class="panel"><img src="../static/images/1.jpg" alt="Image 3"/></div>
            <!-- <div class="panel"><img src="./assets/images/2.jpg" alt="Image 4"/></div>
            <div class="panel"><img src="./assets/images/1.jpg" alt="Image 1"/></div>
            <div class="panel"><img src="./assets/images/2.jpg" alt="Image 2"/></div> -->
        </div>
    </div>
    <!--  -->
    <div class="kecheng_box">
        <div class="kecheng">
            <div class="kecheng_head" >
                <p class="title fc333"></p>
                <ul class="kecheng_nav clearfix">
                    <li v-for="(item, index) in tabs" v-bind:class="{current: index == num}" v-on:click="tab(index)"><a>{{item}}</a></li>
                    <li><a href="course.html" target="_blank"></a></li>
                </ul>
            </div>
            <div class="kecheng_body">
                <ul v-for="(itemCons, index) in tabContents" v-show="index == num">
                  <!-- <li v-for="(itemCon, index) in tabContents[index]"  class="tp" v-on:click="$router.push({path: '/detail'})"> -->
                  <li v-for="(itemCon, index) in tabContents[index]"  class="tp">
                    <router-link v-bind:to="'/courseDetail?courseId='+itemCon.id">
                      <div class="pic">
                        <img v-bind:src="itemCon.url"/>
                        <div  class="triangle">
                          <div class="shadow"></div>
                        </div>
                      </div>
                      <p class="title">{{itemCon.word}}</p>
                    </router-link>
                  </li>
                </ul>
            </div>
        </div>
    </div>
    <!--  -->
    <div class="teacher_box">
        <div class="teacher">
            <div class="teacher_head">
                <p class="title fc333 clearfix">
                    
                </p>
            </div>
            <teacherList v-bind:teacherInfo="teacherInfo"></teacherList>
        </div>
        <router-link to="/teacher" class="btn"></router-link>
    </div>
    <!--  -->
    <div class="hudong_box">
          <div class="hudong">
              <div class="hudong_head">
                  <p class="title fc333 clearfix">
                      
                  </p>
              </div>
              <hudong v-bind:hdData="hdData"></hudong>
          </div>
    </div>
  </div>
</template>

<script>
import navbar from '@/components/header'
import teacherList from '@/components/teacher'
import hudong from '@/components/hudong'

export default {
  name: 'app',
  data () {
    return {
      // showBar: true,
      num: 0,
      tabs: ['', ''],
      tabContents: [
        [
          {id: '000', url: './static/images/kc0/kc1.jpg', word: '1'},
          {id: '001', url: './static/images/kc0/kc2.jpg', word: '2'},
          {id: '002', url: './static/images/kc0/kc3.jpg', word: '3'},
          {id: '010', url: './static/images/kc1/kc1.jpg', word: '1'},
          {id: '011', url: './static/images/kc1/kc2.jpg', word: '2'},
          {id: '000', url: './static/images/kc0/kc1.jpg', word: '1'},
          {id: '001', url: './static/images/kc0/kc2.jpg', word: '2'},
          {id: '002', url: './static/images/kc0/kc3.jpg', word: '3'}
        ],
        [
          {url: './static/images/kc9.jpg', word: 'ps'},
          {url: './static/images/kc10.jpg', word: 'PPT'},
          {url: './static/images/kc11.jpg', word: ''},
          {url: './static/images/kc12.jpg', word: ''},
          {url: './static/images/kc13.jpg', word: ''},
          {url: './static/images/kc14.jpg', word: ''},
          {url: './static/images/kc15.jpg', word: ''},
          {url: './static/images/kc16.jpg', word: ''}
        ]
      ],
      teacherInfo: [
        {id: '01', img: './static/images/teacher1.jpg', name: '', department: '', descript: 'java,'},
        {id: '02', img: './static/images/teacher2.jpg', name: '', department: '', descript: ','},
        {id: '03', img: './static/images/teacher3.jpg', name: '', department: '', descript: ','},
        {id: '04', img: './static/images/teacher4.jpg', name: '', department: '', descript: 'photoShop,'}
      ],
      teacherInfo2: [
        {img: './static/images/teacher5.jpg', name: '', department: '2', descript: 'java,2'},
        {img: './static/images/teacher1.jpg', name: '', department: '2', descript: ',2'},
        {img: './static/images/teacher2.jpg', name: '', department: '2', descript: ',2'},
        {img: './static/images/teacher6.jpg', name: '', department: '2', descript: 'photoShop,2'}
      ],
      hdData: [
        {pic: './static/images/tx-1.png', questioner: '', state: '', teacher: '', link: '', course: 'java', con: ' (Overriding)Java'},
        {pic: './static/images/tx-2.png', questioner: '', state: '', teacher: '', link: '', course: '', con: ''},
        {pic: './static/images/tx-3.png', questioner: '', state: '', teacher: '', link: '', course: 'C', con: ''},
        {pic: './static/images/tx-4.png', questioner: '', state: '', teacher: '', link: '', course: '', con: ''},
        {pic: './static/images/tx-5.png', questioner: '', state: '', teacher: '', link: '', course: '', con: ''}
      ]
    }
  },
  components: {
    navbar,
    teacherList,
    hudong
  },
  methods: {
    tab (index) {
      this.num = index
    },
    scrollTop () {
      var hdList = document.getElementById('hudong_list')
      var h = hdList.offsetHeight
      var t = 0
      setInterval(function () {
        t--
        hdList.style.webkitTransform = 'translateY(' + t + 'px)'
        if (t <= -1 * (h / 2)) {
          t = 0
        }
      }, 50)
    }
  },
  mounted () {
    this.scrollTop()
    // this.$store.commit('setCourseId', 'asd')
    // alert(this.$store.state.course.id)
  }
}
</script>





